.button {
    font-size: 14px;
    font-weight: normal;
}

.disabled {
    background-color: rgba(255, 255, 255, 0.37);
}

.secondary {
    border: 5px solid #004D99;
    color: #002E5C;
    background-color: #ffffff;
}

.default {
    background-color: #0066CC;
    color: #ffffff;
}

.red {
    background-color: #CC1439;
    color: #FFFFFF;
}

.default:hover {
    background-color: #0080FF;
}

.red:hover {
    background-color: #FF1A47;
}

.secondary:hover {
    background-color: #E5F0FA;
}

.default:active {
    background-color: #0059B2;
}

.secondary:active {
    background-color: #C9E5FF;
}

.red:active {
    background-color: #B21232;
}

.button:disabled {
}
